@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{font-family:"Roboto", sans-serif !important;background:linear-gradient(-60deg, #565c5e, #455954, #9d7463, #b5c2c7, #afd275);background-size:400% 400%;-webkit-animation:gradientBG 20s ease infinite;animation:gradientBG 20s ease infinite}.profile-pic{box-shadow:inset 12px 12px 15px 0 #000,inset -12px -12px 15px 0 #000}.btn{margin:5px !important;overflow:hidden;display:flex}.btn:hover{box-shadow:inset 6px 6px 10px 0 rgba(0,0,0,0.2),inset -6px -6px 10px 0 rgba(255,255,255,0.5)}.btn.btn-dark{box-shadow:unset}.btn.btn-dark:hover{border:0;box-shadow:inset 6px 6px 5px 0 rgba(0,0,0,0.2),inset -6px -6px 5px 0 rgba(0,0,0,0.2)}@-webkit-keyframes gradientBG{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}@keyframes gradientBG{0%{background-position:0% 50%}50%{background-position:100% 50%}100%{background-position:0% 50%}}

